import React from 'react';
import { graphql } from 'gatsby';
import { css } from '@emotion/core';
import Layout from '../components/shared/Layout';
import SecondaryPageWrapper from '../components/styled/SecondaryPageWrapper';
import TopBanner from '../components/shared/TopBanner';
import Container from '../components/styled/Container';
import { CopyHeading, BodyText } from '../components/styled/Typography';
import Team from '../components/shared/Team';
import { viewports } from '../utils/variables';

const AboutPage = ({ data }) => {
  const {
    wordpressPage: { acf },
  } = data;
  return (
    <Layout theme="secondary">
      <SecondaryPageWrapper>
        <TopBanner heading={acf.banner_heading} image={acf.banner_image} />
        <Container style={containerCSS} theme="secondary">
          <div css={contentCSS}>
            <CopyHeading theme="large">{acf.about_heading}</CopyHeading>
            <BodyText theme="large">{acf.about_copy}</BodyText>
          </div>
          <Team heading={acf.team_heading} members={acf.team} />
        </Container>
      </SecondaryPageWrapper>
    </Layout>
  );
};

export default AboutPage;

const containerCSS = css`
  padding: 3.125rem 0 5rem;
`;

const contentCSS = css`
  margin: 0 auto;

  @media ${viewports.medium} {
    width: 80%;
  }
`;

export const query = graphql`
  query aboutQuery($page_id: Int = 75) {
    wordpressPage(wordpress_id: { eq: $page_id }) {
      title
      acf {
        banner_heading
        banner_image {
          localFile {
            childImageSharp {
              fluid(maxWidth: 1920) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
        about_heading
        about_copy
        team_heading
        team {
          image {
            localFile {
              childImageSharp {
                fluid(maxWidth: 500) {
                  ...GatsbyImageSharpFluid_withWebp_noBase64
                }
              }
            }
          }
        }
      }
    }
  }
`;
