import React from 'react';
import { css } from '@emotion/core';
import BackgroundImage from '../styled/BackgroundImage';
import Container from '../styled/Container';
import { colors, styles } from '../../utils/variables';

const TopBanner = ({ heading, image }) => {
  return (
    <section css={baseCSS}>
      <BackgroundImage image={image} style={imageCSS} />
      <Container>
        <h2 css={headingCSS}>{heading}</h2>
      </Container>
    </section>
  );
};

export default TopBanner;

const baseCSS = css`
  align-items: flex-end;
  display: flex;
  height: 35vh;
  overflow: hidden;
  position: relative;
  z-index: 0;

  &:after {
    background-color: #fff;
    bottom: 1.875rem;
    content: '';
    height: 2px;
    left: 0;
    position: absolute;
    right: 0;
    z-index: 2;
  }
`;

const imageCSS = css`
  height: 100%;
  position: absolute;
  width: 100%;
  z-index: 0;

  &:before {
    background-color: rgba(0, 0, 0, 0.15);
    bottom: 0;
    content: '';
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
  }
`;

const headingCSS = css`
  align-items: center;
  background: #fff;
  box-shadow: ${styles.boxShadow};
  color ${colors.black};
  height: 3.125rem;
  display: flex;
  font-size: 2.25rem;
  justify-content: center;
  margin-bottom: 1.875rem;
  position: relative;
  width: 13.125rem;
  z-index: 1;
`;
