import React from 'react';
import { css } from '@emotion/core';
import Img from 'gatsby-image';
import { CopyHeading } from '../styled/Typography';
import { viewports } from '../../utils/variables';

const Team = ({ heading, members }) => {
  return (
    <section css={baseCSS}>
      <CopyHeading>{heading}</CopyHeading>
      <div css={teamCSS}>
        {members.map(member => {
          const fluid = member.image.localFile.childImageSharp.fluid;
          return (
            <div css={imageWrapperCSS} key={fluid}>
              <Img fluid={fluid} css={imageCSS} />
            </div>
          );
        })}
      </div>
    </section>
  );
};

export default Team;

const baseCSS = css`
  margin-top: 4.375rem;
  text-align: center;
`;

const teamCSS = css`
  display: flex;
  flex-wrap: wrap;
  margin: 1.875rem -0.75rem 0;
`;

const imageWrapperCSS = css`
  margin-bottom: 1.25rem;
  padding: 0 0.75rem;
  width: 50%;

  @media ${viewports.medium} {
    width: 25%;
  }
`;

const imageCSS = css`
  width: 100%;
`;
